.global-search-banner {
    background: url("/assets/images/background-green.jpg") center / auto 9rem no-repeat;
    height: 9rem;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;

    .layout-container {
        width: 100%;
    }

    .global-search-label {
        font-weight: 600;
        color: $blue;
        display: block;
        margin: 0 0 .75rem 2rem;
        font-size: $font-m;
    }

    .search-field {
        margin: 0 1rem;
    }

}

@media only screen and (min-width: $one-to-two-col) {
    .global-search-banner {
        height: 13rem;
        background-size: auto 13rem;

        .layout-container {
            max-width: $one-point-five-col;
            padding: 0 1rem;
        }

        .search-field {
            margin: 0;
        }

        .global-search-label {
            margin-left: 1rem;
        }
    }
}

@media only screen and (min-width: $two-to-three-col) {
    .global-search-banner {
        height: 18rem;
        background-size: cover;

        .layout-container {
            max-width: $two-col;
        }

        .global-search-label {
            font-size: $font-xl;
            margin-left: 1.5rem;
        }
    }
}

@media only screen and (min-width: $three-to-four-col) {
    .global-search-banner {
        .search-field {
            margin-left: 2rem;
            margin-right: 2rem;
        }

        .global-search-label {
            font-size: $font-xl;
            margin-left: 3.5rem;
        }
    }
}