.visuallyhidden, .skip-link, .search-refiners .expired-codes input {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.visuallypresent, .skip-link:focus {
  position: unset;
  overflow: unset;
  clip: unset;
  height: unset;
  width: unset;
  margin: unset;
  padding: unset;
  border: unset;
}

