}

input {
  font-family: "Open Sans", sans-serif;
}

select {
  appearance: none;
  position: relative;
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  font-size: inherit;
  color: #0f2d5e;
  margin-left: .25em;
  padding: 0 2.5em 0 .5em;
  background: url("/assets/images/chevron-down-white.svg") right 0.65em top 55%/0.85em no-repeat, linear-gradient(to left, #0f2d5e 2.125em, #fff 2em);
  border: 1px solid #0f2d5e;
  height: 2em;
  border-radius: 1em;
  transition: .1s;
  z-index: 1;
}

select::-ms-expand {
  display: none;
}

select:hover, select:focus {
  background-image: url("/assets/images/chevron-down-blue.svg"), linear-gradient(to left, #99eeb2 2.125em, #fff 2em);
  outline: 0;
}

select:disabled, select[aria-disabled=true] {
  color: #5c5c5c;
  background-image: url("/assets/images/chevron-down-dark-grey.svg");
}

select option {
  font-weight: normal;
}

::placeholder {
  color: #8796ae;
  font-style: italic;
}

:focus, ::-moz-focus-inner {
  outline: 0;
}

#framework-footer :focus {
