}

h1 {
  font-size: 2rem;
  font-weight: normal;
  margin: 1rem 0;
}

.expand {
  transition-property: background-color, border-color;
  transition-duration: .5s, .1s;
}

.expand.initial.collapsed {
  transition: none;
}

.expand.initial.collapsed .expandable {
  transition: none;
}

.expand .expander {
  cursor: pointer;
}

.expand .expandable {
  max-height: 20000px;
  overflow: hidden;
  transition-property: max-height, opacity, padding, margin;
  transition-duration: .3s, .3s, .1s, .1s;
  opacity: 1;
}

.expand.collapsed .expandable {
  max-height: 0;
  opacity: 0;
  visibility: hidden;
  transition-property: max-height, opacity, visibility, padding, margin;
  transition-duration: .3s, .9s, 0s, .3s, .3s;
  transition-delay: 0s, 0s, .3s, .3s, 0s;
}

